<template>
	<div class="spec-preview">
		<img :src="imgObj.imgUrl" />
		<div class="event" @mousemove="handler"></div>
		<div class="big">
			<img :src="imgObj.imgUrl" ref="bigImg" />
		</div>
		<!-- 遮罩层 -->
		<div class="mask" ref="mask"></div>
	</div>
</template>

<script>
export default {
	name: "zoomVue",
	props: ["skuImageList"],
	data() {
		return {
			currentIndex: 0,
		};
	},
	methods: {
		handler(event) {
			let mask = this.$refs.mask;
			let bigImg = this.$refs.bigImg;
			let top = event.offsetY - mask.offsetHeight / 2;
			let left = event.offsetX - mask.offsetWidth / 2;
			if (left <= 0) left = 0;
			if (left >= mask.offsetWidth) left = mask.offsetWidth;
			if (top <= 0) top = 0;
			if (top >= mask.offsetHeight) top = mask.offsetHeight;
			mask.style.top = top + "px";
			mask.style.left = left + "px";
			bigImg.style.top = -2 * top + "px";
			bigImg.style.left = -2 * left + "px";
		},
	},
	computed: {
		imgObj() {
			return this.skuImageList[this.currentIndex] || {};
		},
	},
	mounted() {
		//全局事件总线，获取兄弟组件的index
		this.$bus.$on("getIndex", (index) => {
			//修改当前响应式数据
			this.currentIndex = index;
		});
	},
};
</script>

<style lang="less">
.spec-preview {
	position: relative;
	width: 400px;
	height: 400px;
	border: 1px solid #ccc;

	img {
		width: 100%;
		height: 100%;
	}

	.event {
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 998;
	}

	.mask {
		width: 50%;
		height: 50%;
		background-color: rgba(0, 255, 0, 0.3);
		position: absolute;
		left: 0;
		top: 0;
		display: none;
	}

	.big {
		position: absolute;
		top: 0;
		left: 100%;
		width: 100%;
		height: 100%;
		border: 1px solid #aaa;
		overflow: hidden;
		z-index: 998;
		display: none;
		background: white;

		img {
			width: 200%;
			max-width: 200%;
			height: 200%;
			position: absolute;
			left: 0;
			top: 0;
		}
	}

	.event:hover ~ .mask,
	.event:hover ~ .big {
		display: block;
	}
}
</style>