<template>
	<!-- 头部 -->
	<header class="header">
		<!-- 头部的第一行 -->
		<div class="top">
			<div class="container">
				<div class="loginList">
					<p>尚品汇欢迎您！</p>
					<!-- 没有用户名，未登录 -->
					<p v-if="!userName">
						<span>请</span>
						<!-- //声明式导航 -->
						<router-link to="/login">登录</router-link>
						<router-link to="/register" class="register">免费注册</router-link>
					</p>
					<!-- 有用户名，登录了 -->
					<p v-else>
						<!-- //声明式导航 -->
						<a>{{userName}}</a>
						<a class="register" @click="logout">退出登录</a>
					</p>
				</div>
				<div class="typeList">
					<router-link to="/center">我的订单</router-link>
					<router-link to="/shopcart">我的购物车</router-link>
					<a href="###">我的尚品汇</a>
					<a href="###">尚品汇会员</a>
					<a href="###">企业采购</a>
					<a href="###">关注尚品汇</a>
					<a href="###">合作招商</a>
					<a href="###">商家后台</a>
				</div>
			</div>
		</div>
		<!--头部第二行 搜索区域-->
		<div class="bottom">
			<h1 class="logoArea">
				<router-link to="/home" class="logo">
					<img src="./images/logo.png" alt />
				</router-link>
			</h1>
			<div class="searchArea">
				<form action="###" class="searchForm">
					<input type="text" id="autocomplete" class="input-error input-xxlarge" v-model="keyword" />
					<button class="sui-btn btn-xlarge btn-danger" type="button" @click="goSearch">搜索</button>
				</form>
			</div>
		</div>
	</header>
</template>

<script>
export default {
	name: "headerIndex",

	data() {
		return {
			keyword: "",
		};
	},
	watch: {},
	mounted() {
		//通过全局事件总线清除关键字
		this.$bus.$on("clearKeyword", () => {
			this.keyword = "";
		});
	},

	methods: {
		goSearch() {
			//!!!这个组件是搜索框按钮的 params参数传递的源头，最终传递到对应的路由组件route身上
			//路由传递参数
			// this.$router.push(
			// 	`/serach/${this.keyword}?k=${this.keyword.toUpperCase()}`
			// );
			//代表的是如果有query参数也带过去
			if (this.$route.query) {
				let location = {
					// params参数可传可不传， ''||undefined
					name: "search",
					// path: "/search",
					params: { keyword: this.keyword || "" || undefined },
					// params: { keyword: this.keyword || undefined },
					// query: { k: this.keyword.toUpperCase() },
				};
				location.query = this.$route.query;
				this.$router.push(location);
			}

			// //传参对象写法
			// this.$router.push();
		},
		//退出登录
		async logout() {
			try {
				//通知仓库清除本地数据,如果退出成功
				await this.$store.dispatch("userLogout");
				//回到首页
				this.$router.push("/home");
			} catch (error) {
				alert(error.message);
			}
		},
	},
	computed: {
		//用户名信息
		userName() {
			return this.$store.state.user.userInfo.name;
		},
	},
};
</script>

<style lang="less" scoped>
.header {
	& > .top {
		background-color: #eaeaea;
		height: 30px;
		line-height: 30px;

		.container {
			width: 1200px;
			margin: 0 auto;
			overflow: hidden;

			.loginList {
				float: left;

				p {
					float: left;
					margin-right: 10px;

					.register {
						border-left: 1px solid #b3aeae;
						padding: 0 5px;
						margin-left: 5px;
					}
				}
			}

			.typeList {
				float: right;

				a {
					padding: 0 10px;

					& + a {
						border-left: 1px solid #b3aeae;
					}
				}
			}
		}
	}

	& > .bottom {
		width: 1200px;
		margin: 0 auto;
		overflow: hidden;

		.logoArea {
			float: left;

			.logo {
				img {
					width: 175px;
					margin: 25px 45px;
				}
			}
		}

		.searchArea {
			float: right;
			margin-top: 35px;

			.searchForm {
				overflow: hidden;

				input {
					box-sizing: border-box;
					width: 490px;
					height: 32px;
					padding: 0px 4px;
					border: 2px solid #ea4a36;
					float: left;

					&:focus {
						outline: none;
					}
				}

				button {
					height: 32px;
					width: 68px;
					background-color: #ea4a36;
					border: none;
					color: #fff;
					float: left;
					cursor: pointer;

					&:focus {
						outline: none;
					}
				}
			}
		}
	}
}
</style>